@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code&display=swap);
*,::after,::before{box-sizing:border-box}html,body,#root{height:100%;width:100%}body{line-height:1.5;font-family:"Poppins",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1rem;font-weight:400;color:white;margin:0;background-image:url(/static/media/bg.daf4188c.jpg);background-repeat:no-repeat;background-size:cover;background-position:top center}code,.code{line-height:1.5;font-size:1rem;font-family:"Fira Code",source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace;font-weight:400;font-feature-settings:'calt' 1;text-rendering:optimizeLegibility}#root{display:flex;flex-direction:column}#root main{flex-shrink:0}#root footer{margin-top:auto}header{background-color:rgba(255,255,255,0.6);padding:1rem 2rem;display:grid;grid-gap:1rem;gap:1rem;grid-template-columns:10rem auto;grid-template-rows:3.75rem}header .brand{width:10rem}header .navbar{text-align:right}header .navbar>*{display:inline-block;padding:1rem 1.5rem;text-decoration:none}main .container{padding:1rem 2rem;max-width:1400px;margin:0 auto}footer{padding:1rem 2rem}

